import './App.css';
import { Navbar, Container, Nav, NavDropdown, Button } from 'react-bootstrap';
import Data from "./Data.js"
import {useState} from 'react';
import Detail from './Detail.js';
import axios from 'axios';
import {Link, Route, Switch} from 'react-router-dom';
import data from './Data.js';

function App() {
  let [shoes, shoesChange] = useState(Data);
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">Shoe shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/">
          <>
          <div className="jumbotron">
            <h1>20% Season Off</h1>
            <p>React-Bootstrap replaces the Bootstrap JavaScript. Each component has been built from scratch as a true React component, without unneeded dependencies like ...</p>
            <Button variant="primary">Go to shopping!</Button>
          </div>
          <div className="container">
            <div className="row">
              {shoes.map((shoe,i)=>{
                return(
                  <Card shoe={shoe} key={i}/>
                )
              })}
            </div>
            <button className="btn btn-primary" onClick={()=>{
              axios.get('https://codingapple1.github.io/shop/data2.json').then((result)=>{
                var newShoesData = [...shoes];
                result.data.map((data)=>{
                  newShoesData.push(data);
                })
                console.log(newShoesData);
                shoesChange(newShoesData)
              }).catch(()=>{alert("실패했어요! 데이터가 보이지 않아요 ㅠㅠ")});
            }}>더보기</button>
          </div>
          </>
        </Route>
        <Route path = "/detail/:id">
          <Detail shoes={shoes}/>
        </Route>
        <Route path="/:id">
          상세 페이지로 가주세요! /0 , /1, /2
        </Route>
      </Switch>
    </div>
  );
}
function Card(props){
  return(
    <div className="col-md-4">
      <img src={`https://codingapple1.github.io/shop/shoes${props.shoe.id+1}.jpg`}></img>
      <h4>{props.shoe.title}</h4>
      <h5>{props.shoe.content}</h5>
      <p>price: {props.shoe.price}</p>
    </div>
  )
}
export default App;
